.frame-child2 {
  height: 406px;
  width: 525px;
  position: relative;
  border-radius: 5px;
  background: linear-gradient(180deg, #93c34c, #465d24);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  display: none;
  max-width: 100%;
}
.health-image-icon {
  height: 357.1px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.rectangle-parent2 {
  align-self: stretch;
  height: 406px;
  border-radius: 5px;
  background: linear-gradient(180deg, #93c34c, #465d24);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 29px 59px 16px 66px;
  max-width: 100%;
  cursor: pointer;
}
.smart-heatlth {
  position: relative;
  font-weight: 600;
}
.electronic-health-records1 {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  padding-left: 32px;
}
.electronic-health-records-container {
  position: relative;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.5);
  display: inline-block;
  max-width: 100%;
}
.uni-eats-name {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 4px;
  max-width: 100%;
}
.mobile-app-uiux2 {
  position: relative;
  font-weight: 600;
  color: #93c34c;
}
.roomify-content,
.smart-health-banking {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 14px;
  max-width: 100%;
}
.smart-health-banking {
  flex: 1;
  gap: 29px;
}
.frame-child3,
.rectangle-3-1 {
  position: relative;
  max-width: 100%;
}
.frame-child3 {
  height: 406px;
  width: 525px;
  border-radius: 5px;
  background: linear-gradient(180deg, #289ec5, #134c5f);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  display: none;
}
.rectangle-3-1 {
  width: 400px;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.rectangle-parent3 {
  align-self: stretch;
  flex: 1;
  border-radius: 5px;
  background: linear-gradient(180deg, #289ec5, #134c5f);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 22px 56px 23px;
  max-width: 100%;
  cursor: pointer;
}
.banking {
  position: relative;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.5);
}
.finora-parent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 4px;
}
.mobile-app-uiux3 {
  position: relative;
  font-weight: 600;
  color: #6b87ed;
}
.smart-health-wrapper,
.smart-health1,
.uni-eats-details {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.uni-eats-details {
  flex-direction: column;
  gap: 8px;
}
.smart-health-wrapper,
.smart-health1 {
  flex-direction: row;
  max-width: 100%;
}
.smart-health1 {
  flex: 1;
  gap: 20px;
}
.smart-health-wrapper {
  align-self: stretch;
  padding: 0 9px 0 14px;
  box-sizing: border-box;
  text-align: left;
  font-size: 24px;
  color: #fff;
  font-family: Inter;
}
@media screen and (max-width: 750px) {
  .rectangle-parent2 {
    padding-left: 33px;
    padding-right: 29px;
    box-sizing: border-box;
  }
  .uni-eats-name {
    flex-wrap: wrap;
  }
  .smart-health-banking {
    min-width: 100%;
  }
  .rectangle-parent3 {
    padding-left: 28px;
    padding-right: 28px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .rectangle-parent2 {
    padding-top: 20px;
    padding-bottom: 20px;
    box-sizing: border-box;
  }
  .electronic-health-records-container,
  .mobile-app-uiux2,
  .smart-heatlth {
    font-size: 19px;
  }
  .rectangle-parent3 {
    padding-top: 20px;
    padding-bottom: 20px;
    box-sizing: border-box;
  }
  .banking,
  .mobile-app-uiux3 {
    font-size: 19px;
  }
}
